body {
    color: #003876;
}

div {
    box-sizing: border-box;
}

.processo {
    padding: 15px;
}

.processo:focus {
    outline: 4px solid yellow;
}

.div-wrap-flex {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    -webkit-align-items: baseline;
            align-items: baseline;
    -webkit-justify-content: center;
            justify-content: center;
    max-width: 100%;
}

.processo-div-component {
    display: relative;
    background: rgb(239, 242, 245);
    padding: 6px;
    margin: 12px;
    border: 2px solid rgb(178, 189, 239);
    border-radius: 5px;
    font-size: 1.1em;
    box-shadow: 5px 5px 4px rgba(0, 0, 0, 0.1);
    max-width: 250px;
}

.processo-div-component:hover {    
    box-shadow: 5px 5px 4px rgba(0, 0, 0, 0.12);
    -webkit-transform: scale(1.02, 1.02);
            transform: scale(1.02, 1.02);
}

.span-processo-title {
    font-weight: bold;
    cursor: pointer;
}

.div-processo-caracteristicas {
    font-size: 0.9em;
    margin-top: 4px;
    font-family: 'Segoe UI', 'Tahoma', 'Geneva', 'Verdana', 'sans-serif';
    font-size: 0.8em;
    padding: 4px;
}

.listagem-div {
    margin: 6px;
    padding: 4px;    
}

.botoes-copiar-div {
    -webkit-justify-content: space-between;
            justify-content: space-between;
}

button {
    border: 2px solid #2e6e9e;
    background: rgb(225, 231, 236);
    padding: 6px;
    margin-bottom: 2px;    
}

.obsdata1 {    
    position: absolute;
    border-radius: 12px;    
    -webkit-transform: rotate(-7.3deg) translate(-0.3em, -1.4em);    
            transform: rotate(-7.3deg) translate(-0.3em, -1.4em);
    font-weight: 700;
    color: rgb(0, 0, 0);
    background-color: rgba(255, 420.4, 0, 0.65);
    font-size: 0.7em;
}

.opa {
    background-color: rgba(255, 0, 0, 0.65);
    font-size: 1.2em;
}

.obserro {
    border: 2px solid red;
    border-radius: 12px;
    -webkit-transform: rotate(-22deg) translate(1em, -2em);
            transform: rotate(-22deg) translate(1em, -2em);
    font-weight: 700;
    color: rgb(0, 0, 0);
    background-color: rgba(255, 0, 0, 0.7)
}

.obscopiado {
    border: 2px solid red;
    border-radius: 12px;
    -webkit-transform: rotate(-20deg) translate(1em, -2em);
            transform: rotate(-20deg) translate(1em, -2em);
    font-weight: 700;
    color: rgb(0, 0, 0);
    background-color: rgba(69, 252, 160, 0.5)
}

.obsdeletado {
    border: 2px solid red;
    border-radius: 12px;
    -webkit-transform: rotate(-20deg) translate(1em, -2em);
            transform: rotate(-20deg) translate(1em, -2em);
    font-weight: 700;
    color: rgb(0, 0, 0);
    background-color: rgba(255, 8, 8, 0.5)
}

button:focus {
    background: rgb(235, 240, 255);
}

a {
    text-decoration: none;
    cursor: pointer;
}

a:hover {
    -webkit-text-decoration: wavy;
            text-decoration: wavy;
    -webkit-text-decoration-color: cornflowerblue;
            text-decoration-color: cornflowerblue;
}

.div-processo-caracteristicas-digito {
    background-color: rgb(251, 255, 36, 0.65);
    padding: 8px;
    margin: 5px;
    border-radius: 50px;
    border-color: #2e6e9e;
    border-width: 4px;
    size: 1.3em;
    font-size: 1.3em;
    font-weight: bolder;
    font-family: 'Courier New', Courier, monospace;
    position: absolute;
    -webkit-transform: translate(80px, -20px) rotateZ(-12deg);
            transform: translate(80px, -20px) rotateZ(-12deg);
    z-index: 6;

}
body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

